import React from "react";
import PageWrapper from "../wrapper";
import { Box, Card, CardContent, Link, Typography, Button } from "@mui/material";
import { theme_dark, theme_light } from "../../../theme";
import ArrowRightIcon from '@mui/icons-material/ArrowRight';
import { projects_txt, projects } from "../website-text";
import { GitHub } from "@mui/icons-material";

const GITHUB_LINK = (props) => {
  const link = props.link;
  const name = props.name;
  return (
    <>
      { (!!link) ? 
        <Button 
          href={link} 
          target="_blank" 
          rel="noopener noreferrer" 
          sx={{
            color: theme_dark, 
            border: `1px solid ${theme_dark}`,
            marginBottom: '1em',
            '&:hover': {
              color: theme_light,
              backgroundColor: theme_dark,
            }
          }}
        >
          <GitHub sx={{marginRight: '10px'}}/>
          GitHub: {name}
        </Button>
        :
        <Button 
          disabled 
          variant="outlined" 
          style={{ color: theme_dark, borderColor: theme_dark, marginBottom: '1em' }}
        >{'Code is Private (ask me if you\'re interested)'}</Button>
      }
    </>
  );
}

const Projects = () => {
  return (
    <PageWrapper>
      <Box sx={{display: 'flex', justifyContent: 'center', marginBottom: '2em'}}>
        <Typography variant="h3">Projects</Typography>
      </Box>
      <Box sx={{display: 'flex', flexDirection: 'column', textAlign: 'start', marginBottom: '2em'}}>
        <Typography sx={{marginBottom: '2em'}}>{projects_txt}</Typography>
        {projects.map(project => (
          <Card key={project.name} sx={{color: theme_dark, backgroundColor: theme_light, marginBottom: '1em'}}>
            <CardContent>
              <Typography variant="h5">{project.name}</Typography>
              <Typography sx={{fontSize: 'small', marginBottom: '1em'}}>{`(${project.time})`}</Typography>
              <Typography sx={{marginBottom: '1em'}}>{project.description}</Typography>
              {/* {exp.accomplished.map(item => (
                <Typography sx={{display: 'flex', alignItems: 'center', margin: '5px 0'}}><ArrowRightIcon/>{item}</Typography>
              ))} */}
              <GITHUB_LINK name={project.name} link={project.link} />
            </CardContent>
          </Card>
        ))}
      </Box>
    </PageWrapper>
  );
}

export default Projects;